
  import { defineComponent, ref } from "vue";

  export default defineComponent({
    emits: {
      onAddItem({ content }): boolean {
        if (content === "") {
          alert("Please specify task!");
          return false;
        }
        return true;
      },
    },

    setup(props, { emit }) {
      const input = ref();

      function resetInput() {
        input.value.value = "";
        input.value.focus();
      }

      function onAddItem() {
        emit("onAddItem", input.value.value);
        resetInput();
      }
      return { input, resetInput, onAddItem };
    },
  });
